function Beregning() {
    let BilensElement = document.getElementById("BilPris"); //her får jeg fat i det input element, hvor man skriver sin pris ind.
    let BilensPris = parseInt(BilensElement.value); //den her variable sætter jeg "ligemed" inputs element value som er prisen og som laver det om til int;.
    let BilTypeElement = document.getElementById("BilTypeSelect"); //her får jeg fat i det select element som har valgt.
    let BilType = BilTypeElement.value; //den variable sætter jeg det "Ligemed" f.ks det som bliver valgt i selected Element.
    var result = AfgiftBeregning(BilType, BilensPris);
    let outputelement = document.getElementById("output");
    outputelement.innerHTML = "Afgiften er: " + result.toString();
}
function AfgiftBeregning(bilType, pris) {
    switch (bilType) {
        case "Bil":
            {
                if (pris <= 200000) {
                    let Bilafgift = pris * 0.85;
                    return Bilafgift;
                }
                else {
                    let Bilafgift = (pris * 1.5) - 130000;
                    return Bilafgift;
                }
            }
        case "Elbil":
            {
                if (pris <= 200000) {
                    let ElBilafgift = (pris * 0.85) * 0.20;
                    return ElBilafgift;
                }
                else {
                    let ElBilafgift = ((pris * 1.5) - 130000) * 0.20;
                    return ElBilafgift;
                }
            }
    }
}
let BeregnAfgift = document.getElementById("BeregnAfgift");
BeregnAfgift.addEventListener("click", Beregning);
